.position_summary {
    margin: 0 2px;

    &__progress_container {
        display: block;
        position: absolute;
        width: 80%;
        height: 80%;
        bottom: 0;
        left: 0;

    }

    &__loading {
        width: 300px;
        height: 300px;
        margin: 0 auto;
        position: fixed;
        left: 50%;
        top: 50%;
        margin-left: -150px;
        margin-top: -150px;
    }

    &__width {
        min-width: 700px;
    }

    &__items {
        background-color: lavender;
        margin: 0 15px 15px 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        min-height: 100px;
        border-radius: 15px;
        font-size: 1.5rem;
    
        &__text-content {
          margin: 0 2px;
        }
    }
    
    
}
.MuiAccordionSummary-content {
    margin-top: 0!important;
    margin-bottom: 0!important;
}